:root {
	--custom-easing: cubic-bezier(.34, .1, .85, 1.04);
}

.wp-block-itmar-logo-anime,
.wp-block-itmar-tea-time {
	/*=============== 全ブロック共通 =================*/

	position: fixed !important;
	top: 0;
	left: 0;
	background: transparent;
	color: #ffffff;
	width: 100vw;
	height: 100vh;

	/* Loading背景画面　*/
	#splash {
		/*fixedで全面に固定*/
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		z-index: 1000;
		text-align: center;
		//transition: opacity 1s ease 0.8s;

		&.disappear {
			opacity: 0;
		}
	}

	/* Loading画像中央配置　*/
	#splash_logo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: opacity 1s ease 0.8s;

		&.disappear {
			opacity: 0;
		}
	}

	/*=============== tea-timeブロック内の指定 =================*/
	.coffee {
		display: flex;
		justify-content: center;
		width: 20vw;
		height: 50vh;
		background-color: transparent;

		&_text {
			position: absolute;
			bottom: 240px;
			width: 136px;
			font-family: serif;

			>span {
				position: absolute;
				display: block;
				font-size: 16px;
				offset-path: path("m61.498,27.295c9.366,9.437,4.071,22.205-6.929,23.205-10.006.91-19.834-7.567-19.834-23.475,0-13,12.942-24.525,31.834-24.525,18.704,0,25.174,19.976,25,32-.231,16.017-7.03,24.708-13,34-18.22,28.357-18.22,49.926-13,58");
				opacity: 0;

				&.play {
					animation: move-text 3.2s linear infinite, distort-text 3.2s linear infinite;
				}
			}

			@keyframes move-text {
				0% {
					offset-distance: 100%;
					opacity: 0;
				}

				10%,
				90% {
					opacity: 1;
				}

				100% {
					offset-distance: 0;
					opacity: 0;
				}
			}

			@keyframes distort-text {
				45% {
					transform: scaleY(1);
				}

				65% {
					transform: scaleY(1.8);
				}

				85% {
					transform: scaleY(1);
				}

				100% {
					transform: scaleY(0.5);
				}
			}
		}

		&_cup {
			position: absolute;
			bottom: 20px;
			display: block;
			width: 136px;
			height: 100px;
			background-image: url("./blocks/tea-time/img/coffee_cup.png");
			background-size: contain;
			background-repeat: no-repeat;
		}
	}



	/*=============== logo-animeブロック内の指定 =================*/

	#logo_anime {
		path {
			fill-opacity: 0;
			/*fillは透過度0で見えない状態*/
			stroke-opacity: 1;
			/*strokeは透過度1で見える状態*/
			transition: fill-opacity .5s;
			/*カラーがつく際のアニメーション0.5秒で変化*/

		}

	}

	/*アニメーション後に.doneというクラス名がで付与された時の指定*/
	#logo_anime {
		&.done {
			path {
				stroke-opacity: 0;
				/*透過度0で見えない状態*/
				fill-opacity: 1;
				/*透過1で見える状態*/
				stroke: none;

			}
		}

	}

	/*画面遷移アニメーション*/
	.fixbg {
		display: block;
		position: fixed;
		z-index: 990;
		width: 100%;
		height: 100vh;
		top: 0;
		left: 0;
		background-color: #fff;
		transition: opacity 1s ease 0.6s;
		opacity: 1;

		/*フェードアウト*/
		&.disappear {
			opacity: 0;
		}

		/*非表示*/
		&.hide {
			display: none;
		}
	}

	.splashbg,
	.splashbg2 {
		display: none;

		&.appear {
			display: block;
			position: fixed;
			z-index: 999;
			width: 100%;
			height: 100vh;
			/*伸びる背景色の設定*/
			animation-duration: 1.2s;
			animation-timing-function: ease-in-out;
			animation-fill-mode: forwards;

			&.virtical_slide {
				top: 0;
				left: 0;
				transform: scaleY(0);
				animation-name: virtical_open;
			}

			&.horizen_slide {
				top: 0;
				left: 0;
				transform: scaleX(0);
				animation-name: horizen_open;
			}

			&.virtical_open {
				animation-name: top_open;
				animation-duration: 1.4s;
				left: 0;
				bottom: 50%;
				transform: scaleY(1);
			}

			&.horizen_open {
				animation-name: left_open;
				animation-duration: 1.8s;
				top: 0;
				right: 50%;
				transform: scaleX(1);
			}
		}
	}

	.splashbg2 {
		&.appear {
			&.virtical_open {
				animation-name: bottom_open;
				animation-duration: 1.4s;
				left: 0;
				top: 50%;
				transform: scaleY(1);
			}

			&.horizen_open {
				animation-name: right_open;
				animation-duration: 1.4s;
				top: 0;
				left: 50%;
				transform: scaleX(1);
			}
		}
	}

	.splashCirclebg {
		display: none;
		position: fixed;
		transform: scale(100);
		/*伸びる背景色の設定*/
		z-index: 999;
		/*丸のスタートの形状*/
		top: calc(50% - 1rem);
		/*50%から円の半径を引いた値*/
		left: calc(50% - 1rem);
		/*50%から円の半径を引いた値*/
		width: 2rem;
		height: 2rem;

		&.appear {
			display: block;
			border-radius: 50%;
			animation-name: scale_down;
			animation-duration: 1s;
			animation-fill-mode: forwards;
		}
	}

	@keyframes scale_down {
		0% {
			/*丸のスタート位置と形状*/
			transform: scale(100);

		}

		100% {
			/*丸の終了位置と形状*/
			transform: scale(0);
			display: none;
			/*終了時は消える*/
		}
	}


	@keyframes virtical_open {
		0% {
			transform-origin: top;
			transform: scaleY(0);
		}

		50% {
			transform-origin: top;
			transform: scaleY(1);
		}

		50.001% {
			transform-origin: bottom;
		}

		100% {
			transform-origin: bottom;
			transform: scaleY(0);
		}
	}

	@keyframes horizen_open {
		0% {
			transform-origin: right;
			transform: scaleX(0);
		}

		50% {
			transform-origin: right;
			transform: scaleX(1);
		}

		50.001% {
			transform-origin: left;
		}

		100% {
			transform-origin: left;
			transform: scaleX(0);
		}
	}

	@keyframes top_open {
		0% {
			transform-origin: top;
			transform: scaleY(1);
		}

		100% {
			transform-origin: top;
			transform: scaleY(0);
		}
	}

	@keyframes bottom_open {
		0% {
			transform-origin: bottom;
			transform: scaleY(1);
		}

		100% {
			transform-origin: bottom;
			transform: scaleY(0);
		}
	}

	@keyframes left_open {
		0% {
			transform-origin: left;
			transform: scaleX(1);
		}

		100% {
			transform-origin: left;
			transform: scaleX(0);
		}
	}

	@keyframes right_open {
		0% {
			transform-origin: right;
			transform: scaleX(1);
		}

		100% {
			transform-origin: right;
			transform: scaleX(0);
		}
	}

}